.filterWrapper {
    background-color: #fff;
    padding: 1rem;
    border-radius: 0.7rem;
    box-shadow: 0px 3px 3px rgba(0,0,0,0.1);
    position: relative;
}

.filterCard {
    transition: height ease 0.2s;
    overflow: hidden;
}

.title {
    font-size: 1.5rem;
}

