.inputsWrapper {
    display: flex;
    flex-wrap: wrap;
}
.btnWrapper {
    margin-top: 10px;
}

.simpleFilterWrapper {
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.simpleFilter {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.simpleReset {
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #999;
    color: #999;
    padding: 0px 8px;
    border-radius: 1rem;
    transition: background-color 0.2s ease;
}
.simpleReset:active {
    background-color: #eee;
}

@media (hover: hover) {
    .simpleReset:hover {
        background-color: #eee;
    }
}


.simpleFilterPlusMinus {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 0.2rem 0.6rem;
    border-radius: 1rem;
    background-color: #bbb;
    color: #fff;
    text-align: center;
    transition: background-color 0.2s ease;
}

.simpleFilterPlusMinus:active {
    background-color: #aaa;
}

@media (hover: hover) {
    .simpleFilterPlusMinus:hover {
        background-color: #aaa;
    }
}


.simpleFilterMiddle {
    cursor: pointer;
    margin: 0 10px;
    padding: 5px 8px 5px 10px;
    background-color: rgb(2, 169, 230);
    color: #fff;
    border-radius: 1.5rem;
    transition: background-color 0.2s ease;
}

.simpleFilterMiddle:active {
    background-color: rgb(1, 145, 196);
}

@media (hover: hover) {
    .simpleFilterMiddle:hover {
        background-color: rgb(1, 145, 196);
    }
}

.simpleExpand {
    cursor: pointer;
    line-height: 10px;
    padding: 0 3rem;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate(-50%);
    background-color: #eee;
    border-radius: 3px;
}

@media screen and (max-width: 1000px){
    .btnWrapper {
        margin-top: 10px;
    }
}

